aside.app-sidebar {
    position: fixed;
    height: 100%;
    padding: 32px 24px;
    background: linear-gradient(329.54deg, #110635b6 0%, #06115596 100%);
  
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  
  aside.app-sidebar{
    width: 140px;
    text-align: center;
  }
    
  aside.app-sidebar footer a:hover,
  aside.app-sidebar footer button:hover {
    background: #119ead;
  }

.header-btn .btn-1{
    padding: 10px;
    background: #fe9d64;
    border-radius: 25px;
	
}
.header-btn .btn-1:hover{
	background:#ED9DCA;
	transform: scale(1.1);
	
}
